/* Staff page */

.staff-wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	.card.person-card {
		max-width: 220px; } }

@media screen and (max-width: 1079px) {
	.staff-wrap {
		justify-content: space-around;
		.card.person-card {
			max-width: 200px;
			margin-left: 1em;
			margin-right: 1em; } } }
