// Hero section
.hero {
	background: rgb(16, 162, 255) url("./img/hero.jpg") center/cover no-repeat;
	height: 100%;
	min-height: 400px;
	color: #fff;
	overflow: hidden;
	.overlay {
		background-color: rgba(16, 162, 255, 0.5);
		mix-blend-mode: multiply;
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 1; }
	video {
		position: relative;
		top: 50%;
		left: 50%;
		min-width: 100%;
		min-height: 100%;
		max-width: initial;
		width: auto;
		height: auto;
		z-index: 0;
		transform: translateX(-50%) translateY(-100%);
		transition: 1s opacity; }
	.site-name {
		width: 100%;
		height: 100%;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		z-index: 2;
		h1 {
			font-size: 100px;
			line-height: 1em;
			color: #fff; }
		h2 {
			font-size: 50px;
			line-height: 1em;
			color: #fff; } }
	.secondary-nav {
		display: flex;
		justify-content: center;
		position: absolute;
		bottom: 0;
		z-index: 2;
		width: 100%;
		max-width: 1000px;
		border-top: 1px solid #FFF;
		padding: 0;
		font-family: $font-main;
		backdrop-filter: blur(10px);
		-webkit-backdrop-filter: blur(10px);
		a {
			flex: 1;
			text-align: center;
			color: #FFF;
			text-decoration: none;
			padding: 1em 0;
			&:hover {
				background-color: rgba(255,255,255,0.8);
				color: #000; } } } }
