/* Home page */

section {
	&#home-news-section {
			padding: 3em 0; }
	&#home-banner {
		color: #fff;
		background-color: #0088D3;
		padding: 0;
		.banner-row {
			display: flex;
			width: 100%;
			margin: 0;
			img.banner {
				width: 100%;
				height: 100%; }
			.clip {
				clip-path: circle(93% at 20% 50%);
				-webkit-clip-path: circle(93% at 20% 50%); }
			.clip-left {
				clip-path: circle(117% at 100% 50%);
				-webkit-clip-path: circle(117% at 100% 50%); }
			.col-dt-8 {
				padding: 0; }
			.banner-text {
				display: flex;
				align-items: center;
				p {
					max-width: 370px; } } }
		// Push the item to the first position
		.push-first {
			order: 1; }
		// Push theo item to the second position
		.push-last {
			order: 2; }
		.banner-content {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			text-align: center;
			height: 100%;
			h2 {
				color: #fff; } } } }

@media screen and (max-width: 1279px) {
	section#home-news-section {
		padding: 3em 1em; } }

@media screen and (max-width: 991px) {
	section#home-banner {
		.banner-row {
			display: block;
			img.clip {
				clip-path: circle(108% at 50% -49%);
				-webkit-clip-path: circle(108% at 50% -49%); }
			.banner-content {
				margin-bottom: 1em;
				padding: 1.5em 1em; }
			.banner-text {
				margin-bottom: 1em; } } } }

@media screen and (max-width: 768px) {
	.hero {
		.site-name {
			h1 {
				font-size: 66px; }
			h2 {
				font-size: 38px;
				span {
					display: block; } } }
		.secondary-nav {
			display: block;
			overflow-x: auto;
			white-space: nowrap;
			padding: 0;
			text-align: center;
			a {
				display: inline-block;
				padding: 1em 1.5em; } }
		video {
			display: none; } } }
